.main-container .fulu-header {
    min-height: 50px;
    background: #fff;
    padding: 24px;
    font-size: 0;
}
.main-container .fulu-header:after {
    content: '';
    display: table;
    clear: both;
}
.fulu-header .cell {
    float: left;
    min-height: inherit;
    outline: 1px solid #e5e6e6;
    font-size: 14px;
}